import glob from 'fast-glob';
import fs from 'fs/promises';
import path from 'path';

import type {
  RncConfigCompatDependencyConfigAndroid,
  RncConfigCompatReactNativeConfig,
} from './rncConfigCompat.types';
import { fileExistsAsync } from './utils';

export async function resolveDependencyConfigImplAndroidAsync(
  packageRoot: string,
  reactNativeConfig: RncConfigCompatReactNativeConfig | null
): Promise<RncConfigCompatDependencyConfigAndroid | null> {
  const platformReactNativeConfig = reactNativeConfig?.dependency?.platforms?.android;
  if (platformReactNativeConfig === null) {
    // Skip autolinking for this package.
    return null;
  }
  const androidDir = path.join(packageRoot, 'android');

  const globExcludes = [
    'node_modules/**',
    '**/build/**',
    '**/debug/**',
    'Examples/**',
    'examples/**',
    '**/Pods/**',
    '**/sdks/hermes/android/**',
    '**/src/androidTest/**',
    '**/src/test/**',
  ];
  const [manifests, gradles] = await Promise.all([
    glob('**/AndroidManifest.xml', { cwd: androidDir, ignore: globExcludes }),
    glob('build.gradle{,.kts}', { cwd: androidDir, ignore: globExcludes }),
  ]);
  const manifest = manifests.find((manifest) => manifest.includes('src/main/')) ?? manifests[0];
  const gradle = gradles[0];
  if (!manifest && !gradle) {
    return null;
  }

  const packageName =
    platformReactNativeConfig?.packageName ||
    (await parsePackageNameAsync(path.join(androidDir, manifest), path.join(androidDir, gradle)));
  const nativePackageClassName = await parseNativePackageClassName(packageRoot, androidDir);

  if (!nativePackageClassName) {
    return null;
  }

  const packageJson = require(path.join(packageRoot, 'package.json'));
  const packageImportPath =
    platformReactNativeConfig?.packageImportPath ||
    `import ${packageName}.${nativePackageClassName};`;
  const packageInstance =
    platformReactNativeConfig?.packageInstance || `new ${nativePackageClassName}()`;
  const buildTypes = platformReactNativeConfig?.buildTypes || [];
  const dependencyConfiguration = platformReactNativeConfig?.dependencyConfiguration;
  const libraryName =
    platformReactNativeConfig?.libraryName ||
    (await parseLibraryNameAsync(androidDir, packageJson));
  const componentDescriptors =
    platformReactNativeConfig?.componentDescriptors ||
    (await parseComponentDescriptorsAsync(packageRoot, packageJson));
  let cmakeListsPath = platformReactNativeConfig?.cmakeListsPath
    ? path.join(androidDir, platformReactNativeConfig?.cmakeListsPath)
    : path.join(androidDir, 'build/generated/source/codegen/jni/CMakeLists.txt');
  const cxxModuleCMakeListsModuleName =
    platformReactNativeConfig?.cxxModuleCMakeListsModuleName || null;
  const cxxModuleHeaderName = platformReactNativeConfig?.cxxModuleHeaderName || null;
  let cxxModuleCMakeListsPath = platformReactNativeConfig?.cxxModuleCMakeListsPath
    ? path.join(androidDir, platformReactNativeConfig?.cxxModuleCMakeListsPath)
    : null;
  if (process.platform === 'win32') {
    cmakeListsPath = cmakeListsPath.replace(/\\/g, '/');
    if (cxxModuleCMakeListsPath) {
      cxxModuleCMakeListsPath = cxxModuleCMakeListsPath.replace(/\\/g, '/');
    }
  }

  const result: RncConfigCompatDependencyConfigAndroid = {
    sourceDir: androidDir,
    packageImportPath,
    packageInstance,
    dependencyConfiguration,
    buildTypes,
    libraryName,
    componentDescriptors,
    cmakeListsPath,
    cxxModuleCMakeListsModuleName,
    cxxModuleCMakeListsPath,
    cxxModuleHeaderName,
  };
  if (!result.libraryName) {
    delete result.libraryName;
  }
  if (!result.dependencyConfiguration) {
    delete result.dependencyConfiguration;
  }
  return result;
}

/**
 * Parse the `RncConfigCompatDependencyConfigAndroid.packageName`
 */
export async function parsePackageNameAsync(
  manifestPath: string | null,
  gradlePath: string | null
) {
  if (gradlePath) {
    const gradleContents = await fs.readFile(gradlePath, 'utf8');
    const match = gradleContents.match(/namespace\s*[=]*\s*["'](.+?)["']/);
    if (match) {
      return match[1];
    }
  }
  if (manifestPath) {
    const manifestContents = await fs.readFile(manifestPath, 'utf8');
    const match = manifestContents.match(/package="(.+?)"/);
    if (match) {
      return match[1];
    }
  }
  return null;
}

/**
 * Parse the Java or Kotlin class name to for `ReactPackage` or `TurboReactPackage`.
 */
export async function parseNativePackageClassName(
  packageRoot: string,
  androidDir: string
): Promise<string | null> {
  const srcFiles = await glob('**/*Package.{java,kt}', { cwd: androidDir });
  try {
    const className = await Promise.any(
      srcFiles.map((filePath) =>
        parseNativePackageClassNameFromFileAsync(path.join(androidDir, filePath))
      )
    );
    return className;
  } catch {}

  // Early return if the module is an Expo module
  if (await fileExistsAsync(path.join(packageRoot, 'expo-module.config.json'))) {
    return null;
  }

  const allSrcFiles = await glob('**/*.{java,kt}', { cwd: androidDir });
  try {
    const className = await Promise.any(
      allSrcFiles.map((filePath) =>
        parseNativePackageClassNameFromFileAsync(path.join(androidDir, filePath))
      )
    );
    return className;
  } catch {}

  return null;
}

let lazyReactPackageRegex: RegExp | null = null;
let lazyTurboReactPackageRegex: RegExp | null = null;
export async function parseNativePackageClassNameFromFileAsync(filePath: string): Promise<string> {
  const fileContents = await fs.readFile(filePath, 'utf8');

  // [0] Match ReactPackage
  if (!lazyReactPackageRegex) {
    lazyReactPackageRegex =
      /class\s+(\w+[^(\s]*)[\s\w():]*(\s+implements\s+|:)[\s\w():,]*[^{]*ReactPackage/;
  }
  const matchReactPackage = fileContents.match(lazyReactPackageRegex);
  if (matchReactPackage) {
    return matchReactPackage[1];
  }

  // [1] Match TurboReactPackage
  if (!lazyTurboReactPackageRegex) {
    lazyTurboReactPackageRegex =
      /class\s+(\w+[^(\s]*)[\s\w():]*(\s+extends\s+|:)[\s\w():,]*[^{]*TurboReactPackage/;
  }
  const matchTurboReactPackage = fileContents.match(lazyTurboReactPackageRegex);
  if (matchTurboReactPackage) {
    return matchTurboReactPackage[1];
  }

  throw new Error(`Could not find ReactPackage or TurboReactPackage in ${filePath}`);
}

export async function parseLibraryNameAsync(
  androidDir: string,
  packageJson: any
): Promise<string | null> {
  // [0] `codegenConfig.name` from package.json
  if (packageJson.codegenConfig?.name) {
    return packageJson.codegenConfig.name;
  }

  const libraryNameRegExp = /libraryName = ["'](.+)["']/;
  const gradlePath = path.join(androidDir, 'build.gradle');
  // [1] `libraryName` from build.gradle
  if (await fileExistsAsync(gradlePath)) {
    const buildGradleContents = await fs.readFile(gradlePath, 'utf8');
    const match = buildGradleContents.match(libraryNameRegExp);
    if (match) {
      return match[1];
    }
  }

  // [2] `libraryName` from build.gradle.kts
  const gradleKtsPath = path.join(androidDir, 'build.gradle.kts');
  if (await fileExistsAsync(gradleKtsPath)) {
    const buildGradleContents = await fs.readFile(gradleKtsPath, 'utf8');
    const match = buildGradleContents.match(libraryNameRegExp);
    if (match) {
      return match[1];
    }
  }

  return null;
}

export async function parseComponentDescriptorsAsync(
  packageRoot: string,
  pacakgeJson: any
): Promise<string[]> {
  const jsRoot = pacakgeJson?.codegenConfig?.jsSrcsDir
    ? path.join(packageRoot, pacakgeJson.codegenConfig.jsSrcsDir)
    : packageRoot;
  const srcFiles = await glob('**/*.{js,jsx,ts,tsx}', {
    cwd: jsRoot,
    ignore: ['**/node_modules/**'],
  });
  const componentDescriptors = (
    await Promise.all(
      srcFiles.map((filePath) =>
        parseComponentDescriptorsFromFileAsync(path.join(jsRoot, filePath))
      )
    )
  ).filter(Boolean) as string[];

  // Filter out duplicates as it happens that libraries contain multiple outputs due to package publishing.
  // TODO: consider using "codegenConfig" to avoid this.
  return Array.from(new Set(componentDescriptors));
}

let lazyCodegenComponentRegex: RegExp | null = null;
export async function parseComponentDescriptorsFromFileAsync(
  filePath: string
): Promise<string | null> {
  const fileContents = await fs.readFile(filePath, 'utf8');

  if (!lazyCodegenComponentRegex) {
    lazyCodegenComponentRegex =
      /codegenNativeComponent(<.*>)?\s*\(\s*["'`](\w+)["'`](,?[\s\S]+interfaceOnly:\s*(\w+))?/m;
  }
  const match = fileContents.match(lazyCodegenComponentRegex);
  if (!(match?.[4] === 'true') && match?.[2]) {
    return `${match[2]}ComponentDescriptor`;
  }
  return null;
}
